'use client';
import { useEffect, useState } from 'react';
import { AnimatePresence, motion } from 'framer-motion';
import { Input } from '@/components/ui/input';
import { Card, CardContent } from '@/components/ui/Card';
import { MapPin } from 'lucide-react';
import { useDebounce } from 'use-debounce';
import { useToast } from '@/hooks/use-toast';
import { ChevronLeft, ChevronRight } from 'lucide-react';
import { mockHalls } from '@/constants/dataHall';
export interface Hall {
  id: string;
  name: string;
  location: string;
  capacity: number;
  status: string;
}
const AdminHallsPage= ()=> {
  const { toast } = useToast();
  const [halls, setHalls] = useState<Hall[]>([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [debouncedQuery] = useDebounce(searchQuery, 300);
  const [currentPage, setCurrentPage] = useState(1); // حالة Pagination الجديدة
  const [hallsPerPage] = useState(6); // عدد الأحداث في كل صفحة
  const [loading, setLoading] = useState(false);

  const [name, setName] = useState(mockHalls?.name || '');
  const [location, setLocation] = useState(mockHalls?.location || '');
  const [capacity, setCapacity] = useState(mockHalls?.capacity || 1);

  useEffect(() => {
    if (mockHalls) {
      setName(mockHalls.name);
      setLocation(mockHalls.location);
      setCapacity(mockHalls.capacity);
    }
  }, [mockHalls]);

  const fetchHalls = async () => {
    try {
      setLoading(true);
      const res = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/halls?page=${currentPage}&limit=${hallsPerPage}`,
        {
          method: 'GET',
          credentials: 'include',
        }
      );
      const data = await res.json();
      setHalls(data.halls);
    } catch (err: any) {
      toast({
        title: 'Error',
        description: err.message || 'Failed to fetch halls',
        variant: 'destructive',
      });
    } finally {
      setLoading(false);
    }
  };
  useEffect(() => {
    fetchHalls();
  }, [currentPage]);

  // 1. فلترة البيانات التجريبية بناءً على البحث
  const filteredHalls = mockHalls.filter(
    (hall) =>
      hall.name.toLowerCase().includes(debouncedQuery.toLowerCase()) ||
      hall.location.toLowerCase().includes(debouncedQuery.toLowerCase())
  );

  // 2. احسب عدد العناصر للصفحة الحالية من البيانات بعد الفلترة
  const indexOfLastEvent = currentPage * hallsPerPage;
  const indexOfFirstEvent = indexOfLastEvent - hallsPerPage;
  const currentHall = filteredHalls.slice(indexOfFirstEvent, indexOfLastEvent);

  // 3. عدد الصفحات مبني على النتائج المفلترة فقط
  const totalPages = Math.ceil(filteredHalls.length / hallsPerPage);

  // تغيير الصفحة
  const paginate = (pageNumber: number) => setCurrentPage(pageNumber);

  useEffect(() => {
    setCurrentPage(1);
  }, [debouncedQuery]);
  return (
    <>
      {/* Top Info Bar */}
      <motion.div
        className='bg-gradient-to-r from-purple-700 to-blue-600 text-white border-b border-b-purple-200 z-[999]'
        initial={{ y: -60, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.5 }}
      >
        <div className='px-4 md:px-11 py-2 flex flex-wrap items-center justify-between gap-4 text-sm md:text-base'>
          <div className='flex items-center gap-2'>
            <Search className='w-5 h-5 text-white/80' />
            <p className='text-white/80'>Discover new events</p>
          </div>

          {isAuthenticated ? (
            <div className='flex items-center gap-4 text-white/80'>
              <Star className='w-4 h-4 text-yellow-400' />
              {userRole === 'admin' && (
                <Link
                  href='/admin/dashboard'
                  className='hover:underline'
                >
                  Admin Dashboard
                </Link>
              )}
              {userRole === 'host' && (
                <Link
                  href='/host/dashboard'
                  className='hover:underline'
                >
                  Host Dashboard
                </Link>
              )}
              {userRole === 'user' && (
                <Link
                  href='/user/dashboard'
                  className='hover:underline'
                >
                  My Events
                </Link>
              )}
            </div>
          ) : (
            <Link
              href='/auth/login'
              className='text-white hover:text-yellow-300 transition'
            >
              Login to discover more
            </Link>
          )}
        </div>
      </motion.div>

      {/* Main Navigation */}
      <motion.div
        className='bg-white shadow-md z-[999]'
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.2 }}
      >
        <Container className='flex flex-wrap items-center justify-between py-4'>
          {/* Logo */}
          <div className='flex items-center gap-3'>
            <Image
              src={srcImage}
              alt='logo'
              width={50}
              height={50}
            />
            <h1 className='text-2xl font-bold text-purple-800 tracking-tight'>
              UniJoy
            </h1>
          </div>

          {/* Desktop Menu */}
          <div className='hidden md:flex gap-6 items-center'>
            <HeaderMenu />
          </div>

          {/* Mobile Menu */}
          <div className='block md:hidden'>
            <MobileMenu />
          </div>

          {/* Auth Actions */}
          <div className='flex items-center gap-3'>
            {isAuthenticated ? (
              <ProfileDropdown
                user={{ name: 'John Doe' }}
                userRole={userRole}
              />
            ) : (
              <>
                <Link
                  href='/auth/login'
                  className='bg-gradient-to-r from-purple-500 to-blue-600 text-white px-4 py-2 rounded-lg text-sm font-semibold shadow hover:scale-105 transition'
                >
                  Login
                </Link>
                <Link
                  href='/auth/register/user'
                  className='bg-white text-purple-700 border border-purple-500 px-4 py-2 rounded-lg text-sm font-semibold hover:bg-purple-50 transition'
                >
                  Register
                </Link>
              </>
            )}
          </div>
        </Container>
      </motion.div>
    </>
  );
}

export default Header;
